	module.exports = {"data":{"markdownRemark":{"html":"<p>某雑誌に投稿して掲載されたネタ。自分の人生で何らかの投書が掲載されたのはこれが初。\n当時セガが出していた格ゲー “ラストブロンクス” の工藤優作が持っている三節棍がこんな感じに空目したのがアイデアのきっかけ。</p>\n<p>中学時代は 2D 格闘ゲーム (KOF95 から入り、SF ZERO2 や SUPER SF 2X など) が好きだったが、高校入ってからは 3D 格闘ゲームに惚れこんでいたので、 <em>(注意: “得意” とは言っていない)</em> これも例外にもれず好きだった。\nでも個人的には同年代に出ていた “ブラッディロア” のほうが好みで、さらに言うと ”サイキックフォース” に至っては相当ハマり込んでいた。\n<em>(注意: “得意” とは言っていない)</em></p>\n<p>ちなみに、ここで “野菜村” というペンネームを初めて使い始めた。\nこのペンネームは略称 “YASM” 含めて、約一年ほど使い続けた。\nその後 “牧菜(MAKINA)”→“まく(Mc)” と変化していく。</p>","frontmatter":{"cover":null,"date":"1997-09-08T21:00:55+09:00","strDate":"1997/9/8","title":"らすとぶろんくす"}}},"pathContext":{"path":"/ja/blog/1997/09/08-last-bronx/","langKey":"ja"}}

